@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;500;700;800&display=swap);
body * {
  font-family: 'Noto Sans JP', sans-serif;
}

p {
  margin: 0
}

.btn {
  font-size: 13px;
  padding: 7px 20px 9px 20px;
  border-radius: 1px;
}

.btn-sm {
  font-size: 12px;
  padding: 3px 7px;
  border-radius: 2px;
  padding-bottom: 5px;
}

.border-success {
  border-color: #86d096 !important
}

.border-danger {
  border-color: #e299a0 !important;
}

.navbar {
  border-bottom: 1px solid #e6e7ec;
  margin-bottom: 1rem;
  padding: 0;
}

.navbar .navbar-brand {
  font-size: 18px;
  color: #111945;
  font-weight: 700;
  text-transform: uppercase;
  padding-left: 20px;
  padding-right: 30px;
}

.navbar .navbar-nav a.nav-link {
  font-size: 14px;
  color: #000;
  margin-right: 25px;
  font-weight: 500;
  padding: 20px 5px;
  display: inline-block;
}

.navbar .profile img {
  height: 35px;
  border-radius: 20%;
  padding: 0;
}

.profile {
  padding: 13px;
}

.breadcrumb {
  background: none;
  font-size: 12px;
  font-weight: 500;
  padding-left: 0;
  padding-right: 0;
}

.page-info {
  margin: 1rem 0;
}

.page-info .page-title {
  color: #17a2b8;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  border-bottom: 1px solid #17a2b82e;
  padding: 8px 10px;
  border-top: 1px solid #17a2b82e;
}

.project-card {
  /* padding: 40px 20px; */
  border: 1px solid #dadce0;
  border-radius: 15px;
  display: block;
  text-align: center;
  color: #000;
  font-size: 14px;
  max-width: 480px;
  margin-bottom: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .26);
}

.project-card:hover {
  text-decoration: none;
}

.project-card img {
  width: 80px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.project-card .title {
  font-weight: 500;
}

p.description {
  font-size: 12px;
  margin-top: 10px;
}

form label {
  font-size: 14px;
  font-weight: 400;
}

form .form-group .form-control {
  display: block;
  box-shadow: none;
  font-size: 14px;
}

form .form-group .form-control {
  padding: 10px;
  min-height: 45px;
  max-height: 150px;
}

form .section-title {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 500;
  color: #545b62;
  margin-bottom: 10px;
}

form .input-group-text {
  padding: 0px 15px;
  font-size: 14px;
}

/* Project Detail */
.media {
  margin-bottom: 10px;
}

.project-detail .project-logo {
  width: 40px;
}

.project-detail .project-title {
  font-size: 18px;
  margin-bottom: 0;
}

.project-detail .project-description {
  font-size: 12px;
}

.project-detail .card-header {
  padding: 14px;
  font-size: 14px;
}

.project-detail .sf-result .card-title {
  font-size: 50px;
}

.project-config .title {
  font-size: 14px;
  font-weight: 500;
  color: #7b7b7b;
  border-color: #c1bebe;
  background-color: #f5f5f58a;
}

.project-config .value {
  display: inline-block;
  font-size: 13px;
}

.api-control-panel {
  font-size: 14px;
}

.api-control-panel .root-item {
  padding: 20px;
  background-color: #f7f7f7;
  font-weight: 500;
}

.api-control-panel .endpoints {
  margin-left: 10px;
}

.api-control-panel .list-group-item.root {
  border: 0;
  padding: 15px 15px 0px 15px;
}

.api-control-panel .endpoints .list-group-item .badge {
  margin-left: 7px;
}

.api-control-panel #accordion .card-header p {
  padding: 0;
  margin: 0;
  color: #1d2124;
}

.api-control-panel #accordion,
.api-control-panel .response-summary {
  max-height: 600px;
  overflow-y: auto;
}

.api-control-panel .url-lists {
  max-height: 90%;
  overflow-y: auto;
}

.api-control-panel .url-lists li:hover {
  cursor: pointer;
}

.api-control-panel .url-lists .active-url {
  background-color: #6c757d;
  color: #fff;
  margin-left: -10px;
  width: calc(100% + 10px);
  font-weight: 600;
}

.api-control-panel .payload table td {
  font-size: 13px;
}

.api-control-panel .payload .title {
  font-weight: 500;
  margin-bottom: 10px;
}

.api-control-panel .payload .value {
  margin-bottom: 30px;
}

.condition-info {
  margin-top: 30px;
  margin-bottom: 20px;
}

/* profile */
.profile-info .title,
.billing-info .title {
  font-size: 14px;
  font-weight: 500;
}

.profile-info .value,
.billing-info .value {
  margin-top: 5px;
  font-size: 14px;
  font-weight: 400;
}

.jsonEditorContainer {
  width: 100%; 
  height: 400px;
}
